body {
  background: #fff;
  width: 100%;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

ul {
  list-style-type: none;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo-img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav-menu {
  text-align: center;
  line-height: 60px;
}

.menu-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.menu-list .is-active {
  color: #00acdc;
  position: relative;
}

.nav-link {
  text-align: center;
  text-transform: uppercase;
  color: #000;
  cursor: pointer;
  font-size: 12px;
  text-decoration: none;
  display: block;
}

.link {
  margin-left: 20px;
}

.link:first-child {
  margin-left: 0;
}

.nav-link:hover {
  color: #00acdc;
}

.menu-list .is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
